<template>
    <div>
        <el-row>
            <el-button type="primary" round @click="handleCreate">新增学习项目</el-button>
        </el-row>
        <el-table :data="tableData" style="width: 100%">
            <el-table-column label="创建日期" width="220">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.createdAt.substring(0,10) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="标题" width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>名称: {{ scope.row.title }}</p>
                        <p>简介: {{ scope.row.content }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.title }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="所属分类" width="220">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.Catalogue.title }}</span>
                    </template>
                </el-table-column>
    
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                model: {},
                is_create:true,
                tableData: [],
                centerDialogVisible: false
            }
        },
        created(){
            this.init();
        },
        methods: {
            async init(){
                let res = await this.$http.get(`/articles`);
                this.tableData = res.data.articles;
                console.log(res.data.articles)
            },

            async handleSubmit(){
                if(this.is_create){
                let res = await this.$http.post(`/articles`,this.model);
                this.centerDialogVisible = false;
                this.init();
                }
                else{
                let model ={
                    id:this.model.id,
                    title:this.model.title,
                    content:this.model.content,
                }
                let res = await this.$http.put(`/articles`,model);
                console.log(res)
                }
            },
            handleCreate(){
                this.$router.push({name:`article-edit`})
            },
            handleEdit(index, row) {
                this.centerDialogVisible = true;
                this.model = row;
                this.is_create = false;
            },
            handleDelete(index, row) {
                console.log(index, row);
            }
        }
    }
</script>