<template>
    <div>
        <el-row>
            <el-button type="primary" round @click="handleCreate">新增条目</el-button>
        </el-row>
        <el-dialog title="新增条目" :visible.sync="centerDialogVisible" width="35%" center>
            <el-form>
                <el-form-item label="条目名称">
                    <el-input v-model="model.title" />
                </el-form-item>
                <el-form-item label="简介">
                    <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="model.content" />
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" native-type="submit" @click="handleSubmit">提 交</el-button>
            </span>
        </el-dialog>
        <el-table :data="tableData" style="width: 100%">
            <el-table-column label="创建日期" width="220">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    <span style="margin-left: 10px">{{ scope.row.createdAt.substring(0,10) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="条目名称" width="180">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>名称: {{ scope.row.title }}</p>
                        <p>简介: {{ scope.row.content }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag size="medium">{{ scope.row.title }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                model: {},
                is_create:true,
                tableData: [],
                centerDialogVisible: false
            }
        },
        created(){
            this.init();
        },

        methods: {
            async init(){
                let res = await this.$http.get(`/catalogues`);
                this.tableData = res.data.catalogues;
            },

            async handleSubmit(){
                if(this.is_create){
                let res = await this.$http.post(`/catalogues`,this.model);
                this.centerDialogVisible = false;
                this.init();
                }
                else{
                let model ={
                    id:this.model.id,
                    title:this.model.title,
                    content:this.model.content,
                }
                let res = await this.$http.put(`/catalogues`,model);
                console.log(res)
                }
            },
            handleCreate(){
                this.centerDialogVisible = true;
                this.model = {};
                this.is_create = true;
            },
            handleEdit(index, row) {
                this.centerDialogVisible = true;
                this.model = row;
                this.is_create = false;
            },
            handleDelete(index, row) {
                console.log(index, row);
            }
        }
    }
</script>